import React, { useState } from "react";
import { Button } from "grommet";
import Box from '../Box'

const Home = ({ onClick, disabled, message, ...props }) => {
  
  const [loadingState, setLoadingState] = useState(false);
  
  return (
    <Box direction="row">
    <Button
      {...props}
      onClick={() => {
        const returnValue = onClick && onClick();
        if (returnValue && returnValue.then) {
          setLoadingState(true);
          returnValue.then((e) => setLoadingState(false));
        }
      }}
      disabled={disabled || loadingState}
    />
    <Box margin="0" justify="center">
      { message }
    </Box>
    </Box>
  );
};

export default Home;
